/* 왼쪽 베이지 박스 */
.left-content {
    flex: 1;
    /* background-color: --background-color;  */
    border-radius: 8px;
    /* 배경 이미지 */
    background-image: url("../images/backgroundBread.png");
    background-size: contain; /* 이미지 크기에 맞게 설정 */
    background-repeat: no-repeat; /* 이미지 반복 없도록 설정 */
    background-position: center;  /* 중앙 정렬 */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
 

  /* 빵집 카드 */
  .main-card {
    position: relative;
    color: var(--font-main-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    border-radius: 8px;
    /* width: 0%; */
    max-width: 80%; 
    /* min-width: 300px;   최소 300px */
    margin: auto;
  }
  
  
/* 하트 버튼(우측 상단) */
.heart-btn {
  position: absolute;
  top: 3.5rem;
  right: 1rem;
  width: 24px;
  height: 24px;

  background: none;
  background-image: url("../images/emptyHeart.png");
  background-size: contain; /* 이미지 크기에 맞게 설정 */
  background-repeat: no-repeat; /* 이미지 반복 없도록 설정 */
  background-position: center;  /* 중앙 정렬 */
  border: none;

  cursor: pointer;
}

/* 클릭 시(좋아요) 적용할 스타일 */
.heart-btn.liked {
  background-image: url("../images/filledHeart.png"); /* 채워진 하트 이미지 */
}
/* 빵집 이름 */
.bakery-name {
  margin-bottom: 2rem;
  font-family: 'GoryeongStrawberry';
  font-size: 2rem;
  font-weight: bold;

}

/* 주소, 영업시간, 설명 */
.bakery-address,
.bakery-hours,
.bakery-desc {
  font-family: 'NanumSquareNeo';
  font-size: 1rem;
  margin: 8px 0;
}

/* 베이커리 메뉴 */
.bakery-menu {
  font-family: 'NanumSquareNeo';
  font-size: 0.9rem;
  font-weight: bold;
  color: var(--font-point-color);
  margin-top: 2rem;
}
/* 태그 목록 */
.bakery-tags {
  margin-top: 1rem;
}

/* 개별 태그 */
.tag {
  display: inline-block;
  background-color: #fdfdfd;
  border-radius: 16px;
  padding: 4px 12px;
  margin: 4px 2px;
  border: 1px solid #ccc;
  font-size: 0.8rem;
  font-weight: bold;
}

/* 새로고침 버튼 */
/* 기존 .refresh는 width 속성을 제거합니다. */
.refresh {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.5rem auto;
}

/* .refresh-btn에 고정된 width를 지정합니다. */
.refresh-btn {
  width: 300px;  /* 고정된 너비 */
  padding: 8px 0;
  background-color: var(--font-point-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
}
