.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background-color: #fff; /* 페이지 전체 배경 */
}
  .right-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }


    /* 본문 레이아웃(왼쪽-오른쪽) */
    .content-layout {
        flex: 1;
        display: flex;
        gap: 2rem;
        margin: 2rem;
      }

       /* 반응형: 화면이 좁아지면 수직 배치 */
  @media (max-width: 768px) {
    .content-layout {
      flex-direction: column;
    }
    .right-map {
      width: 100%;
      height: 300px;
    }
  }